import "./static/css/app.css";
import "./static/css/custom.css";
import { Login, SignUp } from "./components/Auth/Auth";
import { createBrowserRouter, RouterProvider, Navigate } from "react-router-dom";
import { HeaderAdmin } from "./components/admin/Header";
import { Home } from "./components/landingpage/Home";
import { SidebarAdmin } from "./components/admin/Sidebar";
import { DashboardAdmin } from "./components/admin/Dashboard";
import { ChatsAdmin } from "./components/admin/Chats";
import { FAQsAdmin } from "./components/admin/FAQs";
import { UsersAdmin } from "./components/admin/Users";
// import { PrivateRoute } from "./components/Auth/PrivateRoute";
import { Footer } from "./components/landingpage/Footer";
import { Pricing } from "./components/landingpage/Pricing";
import { AboutUs } from "./components/landingpage/AboutUs";
import { ContactUs } from "./components/landingpage/ContactUs";
import { Error404 } from './components/errors/Error404';
import { LandingPage } from './components/landingpage/LandingPage';
import { AdminDashboard } from "./components/admin/AdminDashboard";
import { UserDashboard } from "./components/User/UserDashboard";

function App() {
  // Mock function to check if the user is logged in and their role
  const isLogIn = () => {
    const user = { username: 'admin', is_superuser: true }; // Replace with actual authentication check
    return user;
  };

  // Admin route component
  const AdminRoute = ({ element }) => {
    const user = isLogIn();
    if (user && (user.username === 'admin' || user.is_superuser)) {
      return element;
    }
    return <Navigate to="/error" />;
  };

  // User route component
  const UserRoute = ({ element }) => {
    const user = isLogIn();
    if (user && user.username !== 'admin' && !user.is_superuser) {
      return element;
    }
    return <Navigate to="/error" />;
  };

  const routes = [
    {
      path: "/",
      element: <LandingPage />,
      children: [
        { path: "/", element: <Home /> },
        { path: "aboutus", element: <AboutUs /> },
        { path: "contactus", element: <ContactUs /> },
        { path: "pricing", element: <Pricing /> },
      ],
    },
    {
      path: "/login",
      element: <Login />,
    },
    {
      path: "/signup",
      element: <SignUp />,
    },
    {
      path: "/admin",
      element: <AdminRoute element={<AdminDashboard />} />,
      children: [
        { path: "dashboard", element: <DashboardAdmin /> },
        { path: "chats", element: <ChatsAdmin /> },
        { path: "faqs", element: <FAQsAdmin /> },
        { path: "users", element: <UsersAdmin /> },
      ],
    },
    {
      path: "/user",
      element: <UserRoute element={<UserDashboard />} />,
      children: [
        // Define user-specific routes here
      ],
    },
    {
      path: "/error",
      element: <Error404 />,
    },
    {
      path: "*",
      element: <Error404 />,
    }
  ];

  
  const router = createBrowserRouter(
    routes,
    {
      initialEntries: ["/boopy/"],
      initialIndex: 0,
      basename: "/boopy",
    }
  );
  return (
      <RouterProvider router={router} />
    // return (
    //   <>
    //     <div>
    //       <Header />
    //       <div className="h-16" />
    //       <div style={{ minHeight: "80vh" }}>
    //         <Routes>
    //           <Route path="/" element={<Home />} />
    //           <Route path="/aboutus" element={<AboutUs />} />
    //           <Route path="/contactus" element={<ContactUs />} />
    //           <Route path="/pricing" element={<Pricing />} />
    //         </Routes>
    //       </div>
    //       <Footer />
    //     </div>
    //   </>
    // );
  );
}

export default App;
